import {useParams} from "react-router-dom";
import {useEffect, useState} from "react"
import Movie from "./Home";


function Detail(){
    const [loading, setLoading] = useState(true);
    const [detailMovie, setDetailMovie] = useState([]);

    const {id} = useParams();
    const getMovie = async() => {
        const json = await (
            await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)).json();
            setLoading((current) => !current);
            setDetailMovie(json.data.movie);
    };
    useEffect(() => {
        getMovie();
    }, [])
    console.log({id});
    console.log(detailMovie);
    return (
        <>
        <h1>Movie Detail</h1>
        {loading ? <h3>loading...</h3> : 
            <div>
                <img src={detailMovie.medium_cover_image} alt={detailMovie.title}/>
                <h2>{detailMovie.title}</h2>
                <p>{detailMovie.description_full}</p>
                <ul>
                    {detailMovie.genres ? detailMovie.genres.map((g) => (
                        <li key={g}>{g}</li>
                    )) : null}
                </ul>
                <h4>Torrent Download</h4>
                <ul>
                    {detailMovie.torrents? detailMovie.torrents.map((d) => (
                        <a key={d.url} href={d.url}>{d.url}</a>
                    )) : null}
                </ul> 
            </div>
        }
        </>
    )
}
export default Detail;