import Todo from "./todo"
import CoinTracker from "./CoinTracker"
import { useState } from "react";
import MovieHome from "./movieHome";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Nav } from "react-bootstrap";
import "./style.css";

function App() {
  const [currentTab, setCuttentTab] = useState(0);
  const menuArr = [
    {name : 'todo', content : <Todo />},
    {name : 'coin', content : <CoinTracker />},
    {name : 'movie', content : <MovieHome />},
    
  ]
  const selectMenuHandler = (index) => {
    setCuttentTab(index);
  }
  return(
    <>
    <Nav fill variant="tabs" defaultActiveKey ="todo">
      {menuArr.map((ele, index) => {
        return(
          <Nav.Item  key={index}>
            <Nav.Link eventKey={ele.name} onClick={()=>selectMenuHandler(index)} >{ele.name} </Nav.Link>
          </Nav.Item>
        )
      })}
    </Nav>
    <div className='container'>
      {menuArr[currentTab].content}
    </div>

    </>
  )
}

export default App;