import { useState } from "react";
import { FormControl, InputGroup, Button, ListGroup, Card } from "react-bootstrap";

function Todo() {
  const [todo, setTodo] = useState("");
  const [todos, setTodos] = useState([]);
  const onChange = (Event)=> setTodo(Event.target.value);
  const onSubmit=(Event)=>{
    Event.preventDefault();
    if(todo === "") {
      return;
    }
    // 변경 함수의 첫번째 매개변수에 현재 값을 보냄 : currentArray
    setTodos((currentArray) => [todo, ...currentArray]);
    setTodo("");
  } 
  //console.log(todos);
  return (
    <div>
      <form onSubmit={onSubmit}>
        <InputGroup className="mb-3">
          <FormControl value={todo} onChange= {onChange} placeholder="Write your to do.....!" aria-label="todo" aria-describedby="todoButton"/>
          <Button variant="outline-secondary" id="todoButton">Add</Button>
        </InputGroup>
      </form>
      <hr/>

      <Card className="text-center">
        <Card.Header>My Todo List</Card.Header>
        <Card.Body>
          <ListGroup as="ul" numbered>
          {
            todos.map((item, index) => <ListGroup.Item key={index}>{item}</ListGroup.Item>)
          }
          </ListGroup>
        </Card.Body>
        <Card.Footer>todo list length : {todos.length}</Card.Footer>
      </Card>
    </div>
  )
}

export default Todo;
