import { useEffect, useState } from "react";

function CoinTracker (){
const [loading, setLoading] = useState(true);
const [coins, setCoins] = useState([]);
const [price, setPrice] = useState(0);
const [viewCoin, setViewCoins] = useState([]);
const onChange = (Event) =>{
    setPrice(Event.target.value)
}
const onClick = () => {
    setViewCoins([]);
    coins.map((coin) => price > coin.quotes.USD.price * doller ? setViewCoins((currentArray) => [coin, ...currentArray]) : null);
}
//console.log(viewCoin);
const [doller, setDoller] = useState();
useEffect(()=>{
    fetch("https://api.coinpaprika.com/v1/tickers?limit=40")
    .then((response)=>response.json())
    .then((json) => {
        setCoins(json)
        setLoading(false)
    });
}, [])
useEffect(()=>{
    fetch("https://quotation-api-cdn.dunamu.com/v1/forex/recent?codes=FRX.KRWUSD")
    .then((response)=>response.json())
    .then((json) => {
        setDoller(json[0].basePrice)
    });
}, [])

    return(
        <div> 
            <h3> The Coins! ({coins.length}) </h3>
            {loading ? 
                <strong>loading ....</strong> : 
                <select>
                {
                    coins.map((coin) => <option key={coin.id}>{coin.name} ({coin.symbol}) : {coin.quotes.USD.price} USD</option>)
                }
            </select>}

            <h3>current dollar amount</h3>
            <input value={doller}/> 원
            <br /><br />

            <h3>Please enter the a mount</h3>
            <input value ={price} type="text" placeholder="코인 살 가격" onChange={onChange} id ="price" />원
            <br />
            <button onClick={onClick}>내가 살 수 있는 코인 찾기</button>
            <br /><br />

            {
                viewCoin.length > 0 ? 
                <table style = {{borderColor : "black", border : "1px solid", backgroundColor:"#efefef", padding:"10px", width : '100%'}}>
                <tbody>
                    <tr> 
                        <th>종목명</th>
                        <th>가격</th>
                        <th>살수 있는 수량</th>
                    </tr>
                        {
                            //console.log(viewCoin)
                            //(viewCoin.length > 0 ? <tr><td>{viewCoin.name}</td><td>{viewCoin.quotes.USD.price}</td><td>{Math.ceil(price/viewCoin.quotes.USD.price)}</td></tr> : null)
                            viewCoin.map((coin) => <tr key={coin.id}><td>{coin.name}</td><td>{(coin.quotes.USD.price*doller)}</td><td>{Math.ceil(price/coin.quotes.USD.price)}</td></tr>)
                        }
                </tbody>
            </table>
            : null
            }

        </div>
    )
}

export default CoinTracker;
