import { Button } from "bootstrap";
import PropTypes from "prop-types";
import { Card } from "react-bootstrap";
import {Link} from "react-router-dom";

function MovieItem({id, medium_cover_image, title, summary, genres}){
    return (
        <>
        <Card style={{width : '15rem'}}>
            <Card.Img variant="top" src={medium_cover_image} alt={title} />
            <Card.Body>
                <Card.Title>{title}</Card.Title>
                <Card.Text>{summary}</Card.Text>
                {/* <Button variant="primary"><Link to={process.env.PUBLIC_URL + `/movie/${id}`}>이동</Link ></Button> */}
            </Card.Body>
        </Card>
        </>
    );
}

MovieItem.prototype={
    id : PropTypes.number.isRequired,
    medium_cover_image : PropTypes.string.isRequired.prototype,
    title: PropTypes.string.isRequired,
    summary : PropTypes.string.isRequired,
    genres : PropTypes.arrayOf(PropTypes.string).isRequired,
}
export default MovieItem;